<template>
	<div id="pintuangoods-list">
		<public-header title="拼团列表">
			<div slot="right" class="search-btn" @click="util.jsUrl('/pages/gohome/pintuan/goods/post')">
				<span>添加活动</span>
			</div>
		</public-header>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.status == -1}" @click="onToggleStatus(-1)">不限</div>
					<div class="tab-item" :class="{active: filter.items.status == 0}" @click="onToggleStatus(0)">已下架</div>
					<div class="tab-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">进行中</div>
					<div class="tab-item" :class="{active: filter.items.status == 2}" @click="onToggleStatus(2)">未开始</div>
					<div class="tab-item" :class="{active: filter.items.status == 3}" @click="onToggleStatus(3)">已结束</div>
				</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
				<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="goods-list"
				>
					<div class="goods-item" v-for="(item, index) in records.data">
						<div class="flex-lr padding-10 border-1px-b">
							<div class="goods-img">
								<img :src="item.thumb" class="img-100" alt="">
							</div>
							<div class="goods-info">
								<div class="goods-name flex-lr">
									<div class="name ellipsis">{{item.name}}</div>
									<div class="c-info" v-if="item.status == 1">已上架</div>
									<div class="c-danger" v-else-if="item.status == 0">未上架</div>
								</div>
								<div class="price flex">
									<div class="c-danger font-bold font-16">￥{{item.price}}</div>
									<div class="font-12 c-gray">￥{{item.oldprice}}</div>
								</div>
								<div class="activity-time flex">
									<div>活动时间：{{item.starttime_cn}}~{{item.endtime_cn}}</div>
								</div>
								<div class="c-gray font-14">库存：{{item.total}}</div>
							</div>
						</div>
						<div class="btn-group">
							<router-link tag="div" class="btn-item bg-info" :to="util.getUrl({path: '/pages/gohome/pintuan/goods/post', query:{id: item.id}})">编辑</router-link>
							<div class="btn-item bg-danger" @click="onDeleteGoods(item,index)">删除</div>
						</div>
					</div>
					<div class="loaded" v-if="records.finished">
						<div class="loaded-tips">没有更多了</div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>没有符合条件的数据!</p>
				</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				isRefresh: false,
				showPreLoading: true,
				filter: {
					items: {
						status: -1,
					}
				}
			}
		},
		components: {
			publicHeader
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'manage/pintuan/goods/list',
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
			},
			onDeleteGoods(item, index) {
				var that = this;
				that.util.jspost({
					url: 'manage/pintuan/goods/del',
					data: {
						id: item.id,
					},
					confirm: '确定删除该商品吗？',
					success:function(){
						that.records.data.splice(index, 1);
					}
				});
			},
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
	#pintuangoods-list .content .goods-item{
		margin: 10px;
		background-color: #fff;
		border-radius: 5px;
	}
	#pintuangoods-list .content .goods-item .goods-img{
		width: 70px;
		height: 70px;
		margin-right: 10px;
	}
	#pintuangoods-list .content .goods-item .goods-info{
		width: 75%;
		font-size: 14px;
	}
	#pintuangoods-list .content .goods-item .goods-info .name{
		max-width: 80%;
	}
	#pintuangoods-list .content .goods-item .price{
		padding-top: 5px;
	}
	#pintuangoods-list .content .goods-item .price div:last-child{
		padding-left: 5px;
		text-decoration:line-through
	}
	#pintuangoods-list .content .goods-item .activity-time{
		padding: 5px 0;
		font-size: 14px;
		color: #7a7a7a;
		overflow: hidden;
	}
	#pintuangoods-list .content .goods-item .btn-group{
		display: flex;
		flex-wrap: nowrap;
		justify-content: flex-end;
		align-items: center;
		width: 100%;
		padding: 10px;
	}
	#pintuangoods-list .content .goods-item .btn-item{
		min-width: 60px;
		margin-left: 10px;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
</style>


